/* Dracula Dark Theme */
body {
  background-color: #282a36;
  color: #f8f8f2;
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

html, body {
  height: 100%;
}

/* Ensure the navbar has enough space below it */
.App {
  max-width: 600px;
  width: 100%;
  padding: 20px;
  background-color: #44475a;
  border-radius: 10px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 60px; /* Adjust based on navbar height */
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

label {
  color: #8be9fd;
  font-size: 18px;
  margin-bottom: 10px;
}

input[type="file"] {
  margin-bottom: 20px;
  color: #f8f8f2;
  background-color: #6272a4;
  border: none;
  padding: 10px;
  border-radius: 5px;
}

button {
  background-color: #50fa7b;
  color: #282a36;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #ff79c6;
}

h2 {
  color: #bd93f9;
  text-align: center;
  margin-top: 20px;
}

pre {
  background-color: #44475a;
  color: #f8f8f2;
  padding: 20px;
  border-radius: 10px;
  overflow-x: auto;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}

pre code {
  font-size: 14px;
}
